.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 100px;
  width: 100px;
  outline: black;
  background-size: 100%, 100%;
  background-image: none;
}

.carousel-control-next-icon:after
{
  content: '>';
  font-size: 35px;
  color: black;
  margin-left: -70px;
}

.carousel-control-prev-icon:after {
  content: '<';
  font-size: 35px;
  color: black;
  margin-left: -50px;
}







