    .icon-bar {
        position: fixed;
        top: 70%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
      }
      
      /* Style the icon bar links */
      .icon-bar a {
        display: block;
        text-align: center;
        padding: 16px;
        transition: all 0.3s ease;
        color: white;
        font-size: 20px;
      }
      
      /* Style the social media icons with color, if you want */
      .icon-bar a:hover {
        background-color: #000;
      }
      
